body {
  background-color: #fff;
  text-rendering: optimizeLegibility;
  text-shadow: 0 0 0.0625rem transparent;
  -webkit-font-smoothing: antialiased;
}

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.container {
  width: 100%;
  max-width: 105rem;
  margin: 0 auto;
  background-color: #f5f5f5;
  box-shadow: 0 0.75rem 2rem 0 rgb(0 0 0 / 5%);
  overflow-x: hidden;
  padding-bottom: 100px;
}

.glide__slide {
  border: 1px solid black;
  line-height: 100px;
  margin: 0;
  text-align: center;
}

//.glide__bullets {
//  bottom: -3em;
//}

.glide__arrow {
  border: 0;
  border-radius: 0;
  box-shadow: none;
  text-shadow: none;
//  top: unset;
//  bottom: -77px;
//
  &--left {
    left: 0;
  }

  &--right {
 right: 0;
  }
}

.b-tab {
  display: none;
}

.b-tab.active {
  display: block;
}

.b-nav-tab {
  display: inline-block;
  padding: 20px;
}

.b-nav-tab.active {
  color: #ff4200;
}

.card {
  position: relative;
  overflow: hidden;
  display: flex;

  //max-width: 300px;
  height: 400px;

  border-radius: 15px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
  transition: box-shadow 0.56s ease-in-out;

  &:hover {
    cursor: pointer;
    border-radius: 15px;
    box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px -7px rgba(0, 0, 0, 0.2);
  }

  &:before {
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 52%, rgba(255,255,255,0.004639355742296902) 100%);
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: inline-block;
    z-index: 1;
  }

    &__title{
      align-self: flex-end;
      padding: 15px 30px;
      position: absolute;
      bottom: 0;
      font-family: 'Barlow', sans-serif;
      z-index: 2;
      left: 0;
    }

  &__course {
    display: block;
    color: rgba(255,255,255,.90);
    font-size: 1rem;
    line-height: 1;
    font-weight: 400;
  }

  &__partner {
    display: block;
    color: rgba(255,255,255,.90);
    font-size: 2rem;
    line-height: 1;
    font-weight: 300;
  }

  &__thumbnail{
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
    white-space: normal;

  }

  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.circle {
  stroke: #f8aa28;
  stroke-dasharray: 650;
  stroke-dashoffset: 650;
  -webkit-transition: all 0.5s ease-in-out;
  opacity: 0.3;
}

.play {
  position:absolute;
  display: inline-block;
  -webkit-transition: all 0.5s ease;
  z-index: 2;
  bottom: 20px;

  svg {
    width: 100px;
  }

  .triangle {
    -webkit-transition: all 0.7s ease-in-out;
    stroke-dasharray: 240;
    stroke-dashoffset: 480;
    stroke: #fff;
    transform: translateY(0)
  }


  &:hover {

    .triangle {
      stroke-dashoffset: 0;
      opacity: 1;
      stroke: #e6aa67;
      animation: nudge 0.7s ease-in-out;

      @keyframes nudge{
        0% {
          transform: translateX(0)
        }
        30% {
          transform: translateX(-5px)
        }
        50% {
          transform: translateX(5px)
        }
        70% {
          transform: translateX(-2px)
        }
        100% {
          transform: translateX(0)
        }
      }
    }

    .circle {
      stroke-dashoffset: 0;
      opacity: 1;
    }

  }
}

.tabs {
  display: flex;
  justify-content: center;
  padding: 30px 0;

  .tab {
    border: solid 1px #ebebeb;
    border-radius: 16px;
    box-shadow: none;
    color: #000000;
    font-size: 1rem;
    font-weight: 700;
    line-height: 32px;
    margin: 6px;
    padding: 0 30px;
    font-family: 'Barlow', sans-serif;
    &.active {
      background-color: #e6aa67;
      color: #fbfbfb;
    }
  }
}

h3 {
  font-family: 'Barlow', sans-serif;
  font-size: 2.25rem;
  line-height: 3.125rem;
  font-weight: 300;
  text-align: center;
  margin-bottom: 0;
}






